@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

:root {
  /* Основные цвета */
  --color-primary: #FF2D00;
  --color-primary-light: #FF7A5C;
  --color-primary-dark: #AA1800;

  /* Элементы */
  --color-gray: #C4C4C4;
  --color-gray-2: #E5E5E5;

  /* Сервисные цвета */
  --color-success: #24D68C;

  /* Дополнительные цвета */
  --color-info: #50B5FF;
  --color-accent-purple: #7B7FD1;
  --color-accent-pink: #C37BA0;
  --color-dark-purple: #5D688C;
  --color-orange: #DE9C6B;
  --color-aquamarine: #67C7B4;
  --color-alternative-green: #87B73F;
  --color-dark-blue: #6AAED2;

  /* Фон */
  --color-background-light: #FAFAFA;

  /* Текст */
  --color-text-black: #000000;
  --color-text-gray: #8A8A8E;

  /* Типографика */
  --font-open-sans: 'Open Sans', sans-serif;
  --font-tt-travels-next: 'TT Travels Next', sans-serif; /* Placeholder, assuming custom font */
}

body {
  margin: 0;
  font-family: var(--font-open-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-background-light);
  color: var(--color-text-black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}